body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  color: black;
  text-decoration: none;
}

.none-display {
  visibility: hidden;
  display: none;
}

.anime-info ul {
  padding: 0;
}

.anime-info li {
  list-style-type: none;
}

.viewer-box {
  padding: 20px;
}

.viewer-box-inner {
  border: solid 1px;
  padding: 20px;
}

.viewer-box :hover {
  background-color: #efefef;
}

.viewer-box figure {
  width: 200px;
  margin: 0 auto;
}

.viewer-box img {
  width: 100%;
}

.viewer-box h2 {
  text-align: center;
}

